                        :playsinline="true"
                        :options="dataplay.playerOptions">
          </video-player>
        </el-card>
      </div>
      <div class="text">

<!-- //分割线       <div class="div"></div>-->

        <el-card class="text-card">
          <el-card>
            <span>欢迎来到项目主页</span>
          </el-card>
          <el-card>
             <!--走马灯-->
            <el-carousel height="150px">
