              <p>🎉基于 Vue2 的WEB项目。</p>
              <P>🎉基于github托管,纯静态页面,数据采用Json形式以供参考。</P>
              <p>🎉基于数据驱动视图的理念封装 element-ui。</p>
              <p>🎉基于router页面不刷新显示模块内容。</p>
              <p>🎉基于 Echarts , Echarts-gl 实现图形绘制与数据可视化。</p>
              <p>🎉基于 vue-video-playe 实现封面视频播放功能。</p>
              <p>🎉基于 Sass , less css预处理器扩展语言。</p>
              <p>👍基于node.js或服务器nginx,可Axios+接口实现前后端分离。</p>
              <p>👍所有内容均采用组件化，模块化。</p>
